<template>
  <div id="wrapper">
    <el-container class="container" style="height: 100%">
      <el-header class="navi_header">
          <el-row>
            <el-col :span="24">
              <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb-wrapper child">
                <!--          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>-->
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
              </el-breadcrumb>
            </el-col>
          </el-row>
          <el-row class="title-row">
            <el-col :span="24"><span class="title-text"></span></el-col>
          </el-row>
      </el-header>
      <el-main>
        <div class="search-box">
          <div class="search-row">
            <span class="search-text"></span>
            <template>
              <el-select v-model="sourceSelected" placeholder="" @change="sourceSelectedChangeEvent">
                <el-option
                  v-for="item in sourceOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </template>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text"></span>
          </div>
          <div class="search-row">
            <span class="search-text"></span>
            <template>
              <el-select v-model="typeSelected" placeholder="" @change="typeSelectedChangeEvent"
                         no-data-text="">
                <el-option
                  v-for="item in typeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                  <span style="float: left">{{ item.label }}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                </el-option>
              </el-select>
            </template>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text"></span>
          </div>

          <div class="search-row" v-show="levelShown">
            <span class="search-text"> level </span>
            <template>
              <el-select v-model="levelSelected" placeholder="" @change="typeSelectedChangeEvent"
                         no-data-text="">
                <el-option
                  v-for="item in levelOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
<!--                  <span style="float: left">{{ item.label }}</span>-->
<!--                  <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>-->
                </el-option>
              </el-select>
            </template>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text">Major level type indicatorUV</span>
          </div>

          <div class="search-row">
            <span class="search-text"></span>
            <el-radio-group v-model="displayModeSelected" size="medium" @change="onRangeSelectBtnChangeListener">
              <el-radio-button v-for="mode in displayModes" :label="mode" :key="mode"/>
            </el-radio-group>
<!--            <i class="el-icon-info select-icon"></i>-->
<!--            <span class="hint-text"></span>-->
            <span class="search-text"></span>
            <el-date-picker :class="{'single-date-select': true}"
                            v-model="singleDateSelect" type="date"
                            :placeholder=dateSelectPlaceHolder
                            :readonly=dateSelectReadOnly
                            :disabled=dateSelectReadOnly
                            @change="singleDateSelectEvent" size="small"
                            :picker-options="dateOption"
                            :default-value="dateFrom"
                            value-format="timestamp"
                            v-show="displayModeSelected === ''"/>
<!--            -->
            <el-date-picker
              v-show="displayModeSelected === ''"
              v-model="dateRangeSelected"
              type="daterange"
              :picker-options="dateOption"
              :readonly=dateSelectReadOnly
              :disabled=dateSelectReadOnly
              :default-value="dateFrom"
              value-format="timestamp"
              size="small"
              range-separator=""
              start-placeholder=""
              end-placeholder=""/>
          </div>
          <div class="search-row" style="display: flex; margin-top: 8px">
            <span class="search-text" v-show="displayModeSelected === ''"></span>
            <span class="search-text" v-show="displayModeSelected === ''"></span>
            <span class="latlng-hint-text">90</span>
            <template>
              <div class="latlng_slider" v-show="displayModeSelected === ''">
                <el-slider v-model="latSelected" range :max="90" :min="-90"></el-slider>
              </div>
              <div class="latlng_slider" v-show="displayModeSelected === ''">
                <el-slider v-model="singleLatSelected" :max="90" :min="-90"></el-slider>
              </div>
            </template>
            <span class="latlng-hint-text">90</span>
          </div>
          <div class="search-row" style="display: flex; margin-top: 8px">
            <span class="search-text" v-show="displayModeSelected === ''"></span>
            <span class="search-text" v-show="displayModeSelected === ''"></span>
            <span class="latlng-hint-text">180</span>
            <template>
              <div class="latlng_slider" v-show="displayModeSelected === ''">
                <el-slider v-model="lngSelected" range :max="180" :min="-180"></el-slider>
              </div>
              <div class="latlng_slider single" v-show="displayModeSelected === ''">
                <el-slider v-model="singleLngSelected" :max="180" :min="-180"></el-slider>
              </div>
            </template>
            <span class="latlng-hint-text">180</span>
          </div>
          <div class="search-row">
            <el-button class="search-btn" round v-on:click="searchForSingleDate"></el-button>
          </div>
        </div>
        <div class="display-box" v-show=resultShown v-loading="loading">
          <div id="heatmapChart" style='width: 1200px; height: 800px; overflow: auto; left: 50px' v-if="heatmapShown"></div>
          <div id="lineChart" style='width: 1200px; height: 800px; overflow: auto; left: 50px' v-if="lineChartShown"></div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>

import SideMenu from './SideMenu'

export default {
  name: 'AnalysisDataView',
  components: {SideMenu},
  data () {
    return {
      nameMapping: {},
      info: {},
      typeOptions: [],
      typeSelected: '',
      sourceOptions: [],
      sourceSelected: '',
      displayModes: ['', ''],
      displayModeSelected: '',
      singleDateSelect: '',
      dateSelectPlaceHolder: '',
      dateSelectReadOnly: true,
      dateFrom: '',
      dateTo: '',
      dateRangeSelected: '',
      dateOption: {
        disabledDate: (time) => {
          const _this = this
          return time >= _this.dateTo || time <= _this.dateFrom
        }
      },
      singleLatSelected: 0,
      singleLngSelected: 0,
      latSelected: [-45, 45],
      lngSelected: [-90, 90],
      levelOptions: [],
      levelShownTypes: [],
      levelSelected: '',
      levelShown: false,
      resultShown: false,
      loading: false,
      heatmapShown: false,
      lineChartShown: false,
      lineChartExist: false,
      heatMapExist: false
    }
  },
  mounted () {
    this.initEntries()
  },
  methods: {
    initEntries () {
      const _this = this
      const api = 'data/analysis/init'
      this.$axios.get(api).then(resp => {
        if (resp && resp.status === 200) {
          // 1. 
          let mappingEN = resp.data.mapping_eng
          let mappingCN = resp.data.mapping_cn
          _this.typeOptions = []
          for (let i = 0; i < mappingCN.length; ++i) {
            _this.nameMapping[mappingEN[i]] = mappingCN[i]
          }
          // 
          _this.levelOptions = []
          const levels = resp.data.level.reverse()
          for (let i = 0; i < levels.length; ++i) {
            _this.levelOptions.push({value: levels[i], label: levels[i], key: levels[i]})
          }
          _this.levelShownTypes = resp.data.need_show_level

          // 2. 
          let data = resp.data.data
          for (let i = 0; i < data.length; i++) {
            const cur = data[i]
            _this.info[cur['source']] = cur
            _this.sourceOptions.push({value: cur['source'], label: cur['source'], key: cur['source']})
          }
        }
      })
      // 
      // const d = '2018-01-01'
      // console.log(new Date(d).getTime())
      // console.log(Date.now())
    },
    typeSelectedChangeEvent (val) {
      // 
      const _this = this
      // level
      const source = _this.sourceSelected
      const typeInfo = _this.info[source][val]
      _this.dateFrom = new Date(typeInfo['date_from']).getTime()
      _this.dateTo = new Date(typeInfo['date_to']).getTime()
      _this.singleDateSelectPlaceHolder = ''
      _this.dateSelectReadOnly = false
      _this.singleDateSelect = ''
      _this.dateRangeSelected = ''
      _this.levelShown = _this.levelShownTypes.indexOf(val) !== -1
    },
    sourceSelectedChangeEvent (val) {
      // 
      const _this = this
      const types = _this.info[val]['types']
      _this.typeOptions = []
      _this.singleDateSelectPlaceHolder = ''
      _this.singleDateSelect = ''
      _this.dateRangeSelected = ''
      _this.typeSelected = ''
      _this.dateFrom = ''
      _this.dateTo = ''
      _this.dateSelectReadOnly = true
      _this.levelShown = false
      for (let i = 0; i < types.length; ++i) {
        _this.typeOptions.push({value: types[i], label: _this.nameMapping[types[i]], key: types[i]})
      }
    },
    singleDateSelectEvent (val) {
      // 
    },
    onRangeSelectBtnChangeListener () {
      // 
      let _this = this
      // if (_this.displayModeSelected === '') {
      //   _this.singleLatSelected = 0
      //   _this.singleLngSelected = 0
      // } else {
      //   _this.singleLatSelected = [-45, 45]
      //   _this.singleLngSelected = [-90, 90]
      // }
      _this.dateRangeSelected = ''
      _this.singleDateSelect = ''
    },
    searchForSingleDate () {
      // 
      const _this = this
      // 
      let complete = true
      if (_this.typeSelected === '' || _this.sourceSelected === '') {
        complete = false
      }
      if (_this.displayModeSelected === '' && _this.singleDateSelect === '') {
        complete = false
      }
      if (_this.displayModeSelected === '' && _this.dateRangeSelected === '') {
        complete = false
      }
      if (_this.levelShown && _this.levelSelected === '') {
        complete = false
      }
      if (!complete) {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        })
        return
      }
      _this.resultShown = true
      _this.loading = true
      let api = ''
      let data = {}
      console.log(_this.lng_range)
      if (_this.displayModeSelected === '') {
        api = 'data/analysis/fetch-date-range'
        data = {
          lat: _this.singleLatSelected,
          lng: _this.singleLngSelected,
          type: _this.typeSelected,
          source: _this.sourceSelected,
          timestamp: _this.dateRangeSelected,
          level: _this.levelSelected
        }
      } else {
        api = 'data/analysis/fetch-single-date'
        data = {
          lat_range: _this.latSelected,
          lng_range: _this.lngSelected,
          type: _this.typeSelected,
          source: _this.sourceSelected,
          timestamp: _this.singleDateSelect,
          level: _this.levelSelected
        }
      }
      console.log(data)
      this.$axios
        .post(api, data)
        .then(successResponse => {
          console.log(successResponse)
          if (_this.displayModeSelected === '') {
            _this.heatmapShown = false
            _this.lineChartShown = true
            _this.clearAllDrawings()
            this.$nextTick(() => {
              this.drawRangeLineChart(successResponse)
              _this.lineChartExist = true
            })
            // this.drawRangeLineChart(successResponse)
          } else {
            _this.heatmapShown = true
            _this.lineChartShown = false
            _this.clearAllDrawings()
            this.$nextTick(() => {
              this.drawSingleDayHeatMap(successResponse)
              _this.heatMapExist = true
            })
            // this.drawSingleDayHeatMap(successResponse)
          }
          _this.loading = false
        })
        .catch(failResponse => {
          this.$message({
            showClose: true,
            message: '',
            type: 'error'
          })
          _this.resultShown = true
          _this.loading = false
        })
    },
    clearAllDrawings () {
      const _this = this
      if (_this.heatMapExist) {
        let hmChart = this.$echarts.init(document.getElementById('heatmapChart'))
        hmChart.clear()
        _this.heatMapExist = false
      }
      if (_this.lineChartExist) {
        let lChart = this.$echarts.init(document.getElementById('lineChart'))
        lChart.clear()
        _this.lineChartExist = false
      }
    },
    drawSingleDayHeatMap (successResponse) {
      const lats = successResponse.data.lat
      const lngs = successResponse.data.lng
      const data = successResponse.data.data
      const max = successResponse.data.max_value
      const min = successResponse.data.min_value
      const titleText = successResponse.data.title
      // 
      //  DOM  EChart 
      let hmChart = this.$echarts.init(document.getElementById('heatmapChart'))
      // 
      // prettier-ignore
      const d = data
        .map(function (item) {
          return [item[1], item[0], item[2] || '-']
        })
      console.log(d)
      // console.log(d)
      hmChart.setOption({
        title: {
          text: titleText,
          left: 'center',
          top: '4%'
        },
        tooltip: {
          position: 'top',
          axisPointer: {
            // 
            type: 'shadow' // 'line' | 'shadow'
          },
          // formatter: '{b}<br/>{c[0]}<br/>{c1}'
          formatter: function (params) {
            const data = params.data
            return ': ' + lngs[data[0]] + '<br/>: ' + lats[data[1]] + '<br/>: ' + data[2]
          }
        },
        grid: {
          height: '50%',
          top: '10%'
        },
        xAxis: {
          name: '',
          type: 'category',
          data: lngs,
          splitArea: {
            show: true
          }
        },
        yAxis: {
          name: '',
          type: 'category',
          data: lats,
          splitArea: {
            show: true
          }
        },
        visualMap: {
          min: min,
          max: max,
          calculable: true,
          orient: 'horizontal',
          left: 'center',
          bottom: '25%',
          precision: 5,
          itemWidth: '20',
          itemHeight: hmChart.getWidth() - 400,
          inRange: {
            color: ['#438cf8', '#f8f3a3', '#ff0000'] //  =>
          }
        },
        series: [
          {
            type: 'heatmap',
            data: d,
            label: {
              show: false
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ],
        toolbox: {
          show: true,
          left: '4%',
          top: '2%',
          feature: {
            saveAsImage: {
              show: true,
              excludeComponents: ['toolbox'],
              pixelRatio: 2
            },
            dataZoom: {
              show: true
            }
          }
        }
      })
    },
    drawRangeLineChart (successResponse) {
      const data = successResponse.data.data
      const axis = successResponse.data.time_axis
      const max = successResponse.data.max_value
      const min = successResponse.data.min_value
      const titleText = successResponse.data.title
      // 
      //  DOM  EChart 
      let lChart = this.$echarts.init(document.getElementById('lineChart'))
      // console.log(axis)
      // console.log(data)
      // 
      lChart.setOption({
        title: {
          text: titleText,
          left: 'center',
          top: '4%'
        },
        xAxis: {
          type: 'category',
          name: '',
          data: axis
        },
        yAxis: {
          type: 'value',
          name: '',
          min: min,
          max: max
        },
        series: [
          {
            data: data,
            type: 'line',
            symbolSize: 10,
            symbol: 'circle',
            smooth: true,
            lineStyle: {
              width: 3,
              shadowColor: 'rgba(0,0,0,0.3)',
              shadowBlur: 10,
              shadowOffsetY: 8
            }
            // smooth: true
          }
        ],
        tooltip: {
          position: 'top'
        },
        toolbox: {
          show: true,
          left: '4%',
          top: '2%',
          feature: {
            saveAsImage: {
              show: true,
              excludeComponents: ['toolbox'],
              pixelRatio: 2
            },
            dataZoom: {
              show: true
            }
          }
        }
      })
    },
    dummy () {
      return null
    }
  }
}
</script>

<style scoped>
#wrapper {
  height: 95%;
}

.navi_header {
  background-color: white;
  color: #333;
  height: 90px !important;
  border-radius: 10px;
}

.el-main {
  background-color: #E9EEF3;
  color: #333;
  text-align: center;
  //line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.breadcrumb-wrapper {
  margin-top: 20px;
  width: 100%;
}

.el-col {
  text-align: left;
}

.title-row {
  height: 20px;
  text-align: left;
  margin-top: 8px;
}

.title-text {
  text-align: left;
  margin-right: 12px;
  margin-bottom: 0;
  color: rgba(0,0,0,.85);
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.hint-text {
  color: rgba(98, 97, 97, 0.63);
  text-overflow: ellipsis;
  font-size: 14px;
}

.latlng-hint-text {
  color: rgba(98, 97, 97, 0.63);
  text-overflow: ellipsis;
  font-size: 14px;
  margin-left: 12px;
  margin-right: 12px;
}

.search-box {
  margin-left: -20px;
  margin-right: -20px;
  min-height: 370px;
  background-color: white;
  border-radius: 10px;

}

.display-box {
  margin-left: -20px;
  margin-right: -20px;
  margin-top: 20px;
  min-height: 170px;
  background-color: white;
  border-radius: 10px;
  overflow: auto;
}

.search-row {
  text-align: left;
  padding-top: 20px;
}

.search-text  {
  color: rgba(0,0,0,.85);
  font-weight: 500;
  font-size: 16px;
  margin-left: 23px;
  margin-right: 23px;
}

.select-icon {
  margin-left: 20px;
  margin-right: 10px;
}

.forbidden {
  cursor: no-drop;
}

.latlng_slider {
  width: 400px;
  margin-top: -7px;
}

.search-btn {
  margin-left: 20px;
  margin-bottom: 20px;
}

.el-slider__bar {
  background-color: #E4E7ED !important;
}

// 
::-webkit-scrollbar {
  width: 0 !important;
}
::-webkit-scrollbar {
  width: 0 !important;height: 0;
}

//  sty
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

</style>



