<template>
  <div id="wrapper">
    <el-container class="container" style="height: 100%">
      <el-header class="navi_header">
        <el-row>
          <el-col :span="24">
            <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb-wrapper child">
              <!--          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>-->
              <el-breadcrumb-item></el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
          </el-col>
        </el-row>
        <el-row class="title-row">
          <el-col :span="24"><span class="title-text"></span></el-col>
        </el-row>
        <el-row class="sub-title-row">
          <el-col :span="24"><span class="sub-title-text"></span></el-col>
        </el-row>
      </el-header>
      <el-main>
        <div class="search-box">
          <div class="search-row">
            <span class="search-text"></span>
            <span class="search-text"></span>
            <template>
              <el-select v-model="idxSelected" placeholder=""
                         no-data-text="" filterable :disabled="idxSelected === ''">
                <el-option
                  style="width: 200px"
                  v-for="(item, idx) in station_ids"
                  :key="idx"
                  :label="item"
                  :value="idx">
                </el-option>
              </el-select>
            </template>
            <span class="search-text">|</span>
            <span class="search-text"></span>
            <template>
              <el-select v-model="idxSelected" placeholder=""
                         no-data-text="" filterable :disabled="idxSelected === ''">
                <el-option
                  style="width: 200px"
                  v-for="(item, idx) in locations"
                  :key="idx"
                  :label="item"
                  :value="idx">
                </el-option>
              </el-select>
            </template>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text"></span>
          </div>
          <div class="search-row">
            <span class="search-text"></span>
            <template>
              <el-radio v-model="modelSelect" label="nps">nps</el-radio>
              <el-radio v-model="modelSelect" label="babin">byc</el-radio>
              <el-radio v-model="modelSelect" label="pj">pj</el-radio>
              <el-radio v-model="modelSelect" label="liuli">mgb</el-radio>
            </template>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text"></span>
          </div>
          <div class="search-row">
            <span class="search-text"></span>
            <template>
              <el-select v-model="predictModelSelected" placeholder="">
                <el-option
                  v-for="item in predictModelOptions"
                  :key="item[1]"
                  :label="item[0]"
                  :value="item[1]">
                  <span style="float: left">{{ item[0] }}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{ item[1] }}</span>
                </el-option>
              </el-select>
<!--              <el-cascader v-model="predictModelSelected" placeholder=""-->
<!--                           :options="predictModelOptions" :show-all-levels="false"></el-cascader>-->
            </template>
            <span class="search-text"></span>
            <el-date-picker
              type="daterange"
              v-model="dateRangeSelected"
              :picker-options="dateOption"
              value-format="timestamp"
              size="small"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              :default-value="dateFrom"
              @change="datePickerChangeListener"/>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text"></span>
          </div>
          <div class="search-row">
            <span class="search-text"></span>
            <el-select v-model="predictLen" placeholder="" no-data-text="" filterable>
              <el-option
                style="width: 200px"
                v-for="item in predictLenOptions"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text">48(2)</span>
          </div>
          <div class="search-row">
            <el-button class="search-btn" round v-on:click="onPredictBtnClicked"></el-button>
            <el-button style="margin-left: 20px" icon="el-icon-setting" round v-on:click="dialogVisible = true" circle></el-button>

            <!--          <i class="el-icon-info select-icon"></i>-->
            <!--          <span class="hint-text"></span>-->
          </div>
        </div>
        <el-dialog title="" :visible.sync="dialogVisible" width="50%" center>
          <div class="dialog-search-row">
            <span class="search-text"></span>
            <el-switch
              v-model="psoRequire"
              :disabled="predictModelSelected === 'LSTM(RNN)' || predictModelSelected === 'GRU(RNN)'">
            </el-switch>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text"></span>
          </div>
          <div class="dialog-search-row">
            <span class="search-text"></span>
            <el-input v-model="epoch" @change="resetEpoch" placeholder="" style="width: 100px"></el-input>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text">RNNepoch</span>
          </div>
          <div class="dialog-search-row">
            <span class="search-text"></span>
            <el-input v-model="windowSize" @change="resetWindowSize" placeholder="" style="width: 100px"></el-input>
            <i class="el-icon-info select-icon"></i>
            <span class="hint-text"></span>
          </div>

          <span slot="footer" class="dialog-footer">
            <span class="hint-text"></span>
          </span>
        </el-dialog>
        <div class="display-box">
          <el-row class="title-row" style=" margin-top: 15px;">
            <el-col :span="24"><span class="title-text" style="padding-left: 20px; "></span></el-col>
          </el-row>
          <template>
            <el-table
              class="result-table" :data="tableData" stripe style="width: 95%">
              <el-table-column prop="date" label=""></el-table-column>
              <el-table-column prop="station" label=""></el-table-column>
              <el-table-column prop="height_model" label=""></el-table-column>
              <el-table-column prop="predict_model" label=""></el-table-column>
              <el-table-column prop="pso" label=""></el-table-column>
              <el-table-column prop="range" label=""></el-table-column>
              <el-table-column prop="output_len" label=""></el-table-column>
              <el-table-column prop="mae" label="MAE"></el-table-column>
              <el-table-column prop="rmse" label="RMSE"></el-table-column>
              <el-table-column prop="mape" label="MAPE(%)"></el-table-column>
            </el-table>
          </template>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>

export default {
  name: 'DuctPredict',
  components: {},
  data () {
    return {
      station_ids: [''],
      locations: [''],
      idxSelected: '',
      // 
      // _this.lats = resp.data.lats
      lngs: [],
      lats: [],
      dateSelectReadOnly: true,
      dateFrom: '',
      dateTo: '',
      dateSelectPlaceHolder: '',
      // 
      predictModelSelected: '',
      predictModelOptions: [
        ['', 'SVR'], ['K', 'KNN'], ['', 'DT'], ['', 'RF'], ['', 'GBRT'],
        ['', 'LSTM(RNN)'], ['', 'GRU(RNN)']
      ],
      // predictModelOptions: [
      //   {
      //     value: 'ML',
      //     label: '',
      //     children: [
      //       {value: 'DT', label: ''}
      //     ]
      //   }
      // ],
      psoRequire: false,
      // 
      modelSelect: 'nps',
      // 
      dateRangeSelected: '',
      dateOption: {
        disabledDate: (time) => {
          const _this = this
          return time >= _this.dateTo || time <= _this.dateFrom
        }
      },
      predictLen: '48(2)',
      predictLenOptions: ['24(1)', '48(2)', '72(3)', '96(4)', '120(5)', '144(6)',
        '168(7)', '192(8)', '216(9)', '240(10)',
        '264(11)', '288(12)', '312(13)', '336(14)', '360(15)', '384(16)',
        '408(17)', '432(18)', '456(19)', '480(20)'],
      // 
      dialogVisible: false,
      epoch: 50,
      windowSize: 6,
      // 
      tableData: []
    }
  },
  mounted () {
    this.initEntries()
  },
  methods: {
    initEntries () {
      const _this = this
      const api = 'height/init_entry'
      this.$axios.get(api).then(resp => {
        if (resp && resp.status === 200) {
          console.log(resp)
          _this.station_ids = resp.data.ids
          _this.lats = resp.data.lats
          _this.lngs = resp.data.lngs
          _this.locations = resp.data.locations
          // for (let i = 0; i < _this.station_ids.length; ++i) {
          //   _this.stationsOptions.push({id: _this.station_ids[i], loc: _this.locations[i]})
          // }
          _this.idxSelected = 0
          _this.dateSelectReadOnly = false
          _this.dateFrom = new Date(resp.data.date_from).getTime()
          _this.dateTo = new Date(resp.data.predict_date_to).getTime() // 
          _this.dateSelectPlaceHolder = ''
        }
      })
    },
    datePickerChangeListener (val) {
      // 
      const _this = this
      let interval = val[1] - val[0]
      interval = interval / 1000
      if (interval < 2592000) { // 30
        this.$notify({
          title: '',
          message: '',
          type: 'warning'
        })
        _this.dateRangeSelected = ''
      }
    },
    onPredictBtnClicked () {
      const _this = this
      if (_this.modelSelect === '' || _this.dateRangeSelected === '') {
        this.$notify({
          title: '',
          message: '',
          type: 'warning'
        })
        return
      }
      const api = 'predict/predict'
      const data = {
        id: _this.station_ids[_this.idxSelected],
        date: _this.dateRangeSelected,
        pso: _this.psoRequire,
        predict_model: _this.predictModelSelected,
        height_model: _this.modelSelect,
        epoch: _this.epoch,
        window: _this.windowSize,
        output_len: _this.predictLen
      }
      const loading = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0,0,0,0.5)'
      })
      this.$axios
        .post(api, data)
        .then(successResponse => {
          const code = successResponse.data.code
          if (code === 0) {
            this.$notify({
              title: '',
              message: successResponse.data.msg,
              type: 'success'
            })
            console.log(successResponse.data.table_entry)
            _this.tableData.unshift(successResponse.data.table_entry)
          } else if (code === -1) {
            this.$notify({
              title: '',
              message: successResponse.data.msg,
              type: 'warning'
            })
          }
        })
        .catch(failResponse => {
          _this.loading = false
          console.log(failResponse)
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          })
        })
      loading.close()
    },
    resetEpoch () {
      let str = this.epoch
      this.epoch = str.replace(/[^\d]/g, '').replace(/\./g, '')
      this.epoch = Number(this.epoch)
      if (this.epoch > 1000) {
        this.epoch = 1000
      } else if (this.epoch < 20) {
        this.epoch = 20
      }
    },
    resetWindowSize () {
      let str = this.windowSize
      this.windowSize = str.replace(/[^\d]/g, '').replace(/\./g, '')
      this.windowSize = Number(this.windowSize)
      if (this.windowSize > 12) {
        this.windowSize = 12
      } else if (this.windowSize < 2) {
        this.windowSize = 2
      }
    }
  }
}
</script>

<style scoped>
#wrapper {
  height: 95%;
}

.navi_header {
  background-color: white;
  color: #333;
  height: 120px !important;
  border-radius: 10px;
}

.el-main {
  background-color: #E9EEF3;
  color: #333;
  text-align: center;
//line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.breadcrumb-wrapper {
  margin-top: 20px;
  width: 100%;
}

.el-col {
  text-align: left;
}

.title-row {
  height: 20px;
  text-align: left;
  margin-top: 8px;
}

.sub-title-row {
  height: 20px;
  text-align: left;
  margin-top: 8px;
}

.title-text {
  text-align: left;
  margin-right: 12px;
  margin-bottom: 0;
  color: rgba(0,0,0,.85);
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.sub-title-text {
  text-align: left;
  margin-right: 12px;
  margin-bottom: 0;
  color: rgba(79, 76, 76, 0.85);
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.hint-text {
  color: rgba(98, 97, 97, 0.63);
  text-overflow: ellipsis;
  font-size: 14px;
}

.latlng-hint-text {
  color: rgba(98, 97, 97, 0.63);
  text-overflow: ellipsis;
  font-size: 14px;
  margin-left: 12px;
  margin-right: 12px;
}

.search-box {
  margin-left: -20px;
  margin-right: -20px;
  min-height: 200px;
  background-color: white;
  border-radius: 10px;
}

.display-box {
  margin-left: -20px;
  margin-right: -20px;
  margin-top: 20px;
  min-height: 170px;
  background-color: white;
  border-radius: 10px;
  overflow: auto;
}

.search-row {
  text-align: left;
  padding-top: 20px;
}

.dialog-search-row {
  text-align: left;
  padding-top: 20px;
}

.search-text  {
  color: rgba(0,0,0,.85);
  font-weight: 500;
  font-size: 16px;
  margin-left: 23px;
  margin-right: 23px;
}

.select-icon {
  margin-left: 20px;
  margin-right: 10px;
}
.result-table {
  padding: 20px 0 20px 20px;
}
.search-btn {
  width: 150px;
  margin-left: 20px;
  margin-bottom: 20px;
}

/deep/ .el-dialog {
  border-radius: 15px;
}
</style>



