<script>
export default {
  name: 'NavMenu',
  data () {
    return {
      navList: [
        {name: '/main', navItem: ''},
        {name: '/data-view', navItem: ''},
        {name: '/index', navItem: ''},
        {name: '/admin', navItem: ''}
      ]
    }
  }
}
</script>

<style scoped>
a{
  text-decoration: none;
}

span {
  pointer-events: none;
}

.el-menu {
  text-align: left;
  margin-left: 5px;
}

// 
::-webkit-scrollbar {
  width: 0 !important;
}
::-webkit-scrollbar {
  width: 0 !important;height: 0;
}
.inner::-webkit-scrollbar {
  display: none;
}

</style>



